@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table-responsive {
    height: 500px;
    overflow: scroll;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  }
  thead tr:nth-child(1) th {
    background: rgb(52, 224, 30);
    position: sticky;
    top: 0;
    z-index: 10;
  }
  
  tr:hover {
    background-color: I#ffa;
  }
  
  
  
  
  
  .table tr td:hover {
    background-color: rgb(117, 223, 122);
    color: rgb(20, 66, 107);
    border: 0.5px solid Orgb(12, 12, 12);
  }
  
  .table tr td:hover:nth-child(4) {
    background-color: rgb(231, 83, 115);
    color: aliceblue;
    border: 0.5px solid Orgb(12, 12, 12);
  }
  
  .container-fluid {
    width: 90%;
  
    margin: auto;
  }
.live-indicator-wrapper {
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }




.pulse {
    margin:10px;
    float: left;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #dc3545;
    cursor: pointer;
    box-shadow: 0 0 0 #dc3545;
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
  }
  
  
  @-webkit-keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 #dc3545;
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(204,169,44, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(204,169,44, 0);
    }
  }
  @keyframes pulse {
    0% {
      box-shadow: 0 0 0 0 #dc3545;
    }
    70% {
        box-shadow: 0 0 0 10px rgba(204,169,44, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(204,169,44, 0);
    }
  }
